import React from "react";
const SlotM = (props) => {
  //   let x = "ðŸ˜€";
  //   let y = "ðŸ˜€";
  //   let z = "ðŸ˜€";

  let x = props.x;
  let y = props.y;
  let z = props.z;

  if (x === y && y === z) {
    return (
      <>
        <div className="slot_inner">
          <span className="code_emoji">
            {x} {y} {z}
          </span>
          <hr className="line" />
          <h6 className="output" style={{ color: "black" }}>
            This is matching!
          </h6>
        </div>
      </>
    );
  } else {
    return (
      <>
        <div className="slot_inner">
          <span className="code_emoji">
            {x} {y} {z}
          </span>
          <h6 className="output">Code is not Matching</h6>
        </div>
      </>
    );
  }
};

export default SlotM;
